html,
body,
#root {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
}
.colorWeak {
  filter: invert(80%);
}
.ant-layout {
  min-height: calc(100vh - 72px);
}
.ant-pro-sider.ant-layout-sider.ant-pro-sider-fixed {
  left: unset;
}
.ant-pro-layout .ant-pro-layout-content {
  padding-block: 8px;
  padding-inline: 8px;
}
canvas {
  display: block;
}
body {
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
ul,
ol {
  list-style: none;
}
@media (max-width: 768px) {
  .ant-table {
    width: 100%;
    overflow-x: auto;
  }
  .ant-table-thead > tr > th,
  .ant-table-tbody > tr > th,
  .ant-table-thead > tr > td,
  .ant-table-tbody > tr > td {
    white-space: pre;
  }
  .ant-table-thead > tr > th > span,
  .ant-table-tbody > tr > th > span,
  .ant-table-thead > tr > td > span,
  .ant-table-tbody > tr > td > span {
    display: block;
  }
}
.adminLayout {
  height: 100%;
  background: #fff;
}
.sider {
  background: #fff;
  border-right: 1px solid #f0f0f0;
  border-radius: 8px;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.sider :global .ant-menu {
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  height: calc(100% - 48px);
  border-right: 0;
  border-radius: 8px;
}
.sider :global .ant-menu .ant-menu-item {
  transition: all 0.3s;
  background-color: transparent !important;
  color: rgba(0, 0, 0, 0.65) !important;
}
.sider :global .ant-menu .ant-menu-item:nth-child(1).ant-menu-item-selected {
  background-color: #fffbe6 !important;
  color: #faad14 !important;
}
.sider :global .ant-menu .ant-menu-item:nth-child(1):not(.sider :global .ant-menu .ant-menu-item:nth-child(1).ant-menu-item-selected):hover {
  background-color: rgba(0, 0, 0, 0.06) !important;
  color: rgba(0, 0, 0, 0.88) !important;
}
.sider :global .ant-menu .ant-menu-item:nth-child(2).ant-menu-item-selected {
  background-color: #d1d8e8 !important;
  color: #6a7b8c !important;
}
.sider :global .ant-menu .ant-menu-item:nth-child(2):not(.sider :global .ant-menu .ant-menu-item:nth-child(2).ant-menu-item-selected):hover {
  background-color: rgba(0, 0, 0, 0.06) !important;
  color: rgba(0, 0, 0, 0.88) !important;
}
.sider :global .ant-menu .ant-menu-item:nth-child(3).ant-menu-item-selected {
  background-color: #d1e8d1 !important;
  color: #6a8c6a !important;
}
.sider :global .ant-menu .ant-menu-item:nth-child(3):not(.sider :global .ant-menu .ant-menu-item:nth-child(3).ant-menu-item-selected):hover {
  background-color: rgba(0, 0, 0, 0.06) !important;
  color: rgba(0, 0, 0, 0.88) !important;
}
.sider :global .ant-menu .ant-menu-item:nth-child(4).ant-menu-item-selected {
  background-color: #e8d3d1 !important;
  color: #8c6b6a !important;
}
.sider :global .ant-menu .ant-menu-item:nth-child(4):not(.sider :global .ant-menu .ant-menu-item:nth-child(4).ant-menu-item-selected):hover {
  background-color: rgba(0, 0, 0, 0.06) !important;
  color: rgba(0, 0, 0, 0.88) !important;
}
.sider .siderFooter {
  height: 40px;
  padding: 0 12px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-top: 1px solid #f0f0f0;
  background: #fff;
  position: sticky;
  bottom: 0;
  z-index: 1;
}
.sider .siderFooter .siderTitle {
  font-size: 14px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.85);
}
.sider .siderFooter .trigger {
  font-size: 14px;
  cursor: pointer;
  transition: color 0.3s;
}
.sider .siderFooter .trigger:hover {
  color: #faad14;
}
.content {
  padding: 0 0 0 8px;
  margin: 0;
  height: 100%;
  overflow: auto;
  transition: all 0.3s;
}
.content.contentExpanded {
  margin-left: 0;
}
.content.bss-agent {
  background-color: #d1d8e8;
}
.content.resource-agent {
  background-color: #d1e8d1;
}
.content.assurance-agent {
  background-color: #e8d3d1;
}
.boxLeft,
.boxRight {
  background: #fff;
  border: 2px solid #bfc8b7;
  border-radius: 4px;
  min-height: 400px;
  box-shadow: 0 2px 8px #f0f1f2;
}
.boxLeft .boxTitle,
.boxRight .boxTitle {
  font-size: 16px;
  font-weight: 500;
  text-align: center;
  padding: 8px 8px 0 8px;
}
.boxLeft .boxText,
.boxRight .boxText {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-word;
  white-space: pre-wrap;
  line-height: 1.5;
  padding: 4px 8px 8px 8px;
}
.boxLeft {
  width: calc(24vw - 108px);
}
.boxRight {
  width: calc(48.6vw - 108px);
}
.flowContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.flowContainer .flowStep {
  background: #d1e8d1;
  color: #6a8c6a;
  font-weight: 600;
  border-radius: 8px;
  padding: 8px 12px;
  margin: 0 8px;
  width: calc(9vw - 28px);
  height: 80px;
  font-size: 14px;
  box-shadow: 0 2px 8px #f0f1f2;
  line-height: 1.2;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.flowContainer .flowStepNotReady {
  background: white;
  color: #6a8c6a;
  font-weight: 600;
  border-radius: 8px;
  padding: 8px 12px;
  margin: 0 8px;
  width: calc(9vw - 28px);
  text-align: center;
  font-size: 14px;
  box-shadow: 0 2px 8px #f0f1f2;
}
.flowContainer .flowArrow {
  color: #6a8c6a;
}
.flowContainer .flowArrowNotReady {
  color: #d1e8d1;
}
