.container {
  padding: 16px;
  height: calc(100vh - 150px);
}
.container .card {
  padding: 10px;
  background: white;
  border-radius: 8px;
  border: 1px solid #ddd;
  margin: 10px 0;
  height: 300px;
  text-align: center;
}
.container .orch {
  margin: 10px 0;
  background: #ebe9e9;
  padding: 10px 5px;
}
.container .green {
  color: green;
}
.container .red {
  color: red;
}
.container .blue {
  color: blue;
}
.container .gray {
  color: gray;
}
.container .black {
  color: black;
}
